import {sessionStore} from './session.store';
import {environment} from '../../../environments/environment';
import {PublicClientApplication} from '@azure/msal-browser';
import {msalConfiguration} from '../../../config/msal.config';
import {loginToAzure} from '../../../helpers/msalHelpers';

export const setIdentityToken = (identityToken: string) => {
  sessionStore.update((state) => ({
    ...state,
    identityToken: identityToken,
  }));
}

export const setIdentityTokenByOfficeRuntime = async (): Promise<boolean> => {
  try {
    const userIdentityToken = await OfficeRuntime.auth.getAccessToken();
    setIdentityToken(userIdentityToken);
    return true;
  } catch (exception) {
    console.log(exception);
    return false;
  }
}

export const setIdentityTokenByCurrentLoggedInUser = async () => {
  const userAgentApp: PublicClientApplication = new PublicClientApplication(msalConfiguration);

  const account = userAgentApp.getAllAccounts()[0];
  if (account) {
    const accessTokenRequest = {
      scopes: environment.auth.consentScopes,
      account: account,
    };

    try {
      const accessTokenResponse = await userAgentApp.acquireTokenSilent(accessTokenRequest);
      if (accessTokenResponse.accessToken) {
        setIdentityToken(accessTokenResponse.accessToken);
        return true;
      }
    } catch (e) {
      try {
        loginToAzure(async (status) => {
          if (status) {
            const accessTokenResponse = await userAgentApp.acquireTokenSilent(accessTokenRequest);
            if (accessTokenResponse.accessToken) {
              setIdentityToken(accessTokenResponse.accessToken);
              return true;
            }
          }
          return false;
        }, (error) => {
          console.log(error);
        });
      } catch (e) {
        console.log(e);
      }
    }
  }
  return false;
}

export const clearSessionStore = () => {
  sessionStore.update(state => ({
    ...state,
  }))
}