import {App} from './components/App';
import {initializeIcons} from '@fluentui/font-icons-mdl2';
import {ThemeProvider} from '@fluentui/react';
import {EventType, PublicClientApplication} from '@azure/msal-browser';
import {msalConfiguration} from '../config/msal.config';
import {MsalProvider} from '@azure/msal-react';
import {devTools} from '@ngneat/elf-devtools';
import {initializeFileTypeIcons} from '@fluentui/react-file-type-icons';
import {clearMsalInformationFromLocalStorage} from '../helpers/msalHelpers';
import {environment} from '../environments/environment';
import React from 'react';
import ReactDOM from 'react-dom';
import './index.scss';

initializeIcons();
initializeFileTypeIcons();

if (!environment.production) {
  devTools();
}

let isOfficeInitialized = false;

const title = 'Addin-Enthus';

export const msalPublicClientApplication = new PublicClientApplication(msalConfiguration);

// Add event listener for token acquire failure (e.g. refresh token expired)
msalPublicClientApplication.addEventCallback((e) => {
  if (e.eventType === EventType.ACQUIRE_TOKEN_FAILURE && e.error !== null) {
    clearMsalInformationFromLocalStorage();
    location.reload();
  }
});

const render = (Component) => {
  ReactDOM.render(
    <ThemeProvider>
      <MsalProvider instance={msalPublicClientApplication}>
        <Component
          title={title}
          isOfficeInitialized={isOfficeInitialized}/>
      </MsalProvider>
    </ThemeProvider>,
    document.getElementById('container')
  );
};

/* Render application after Office initializes */
Office.initialize = () => {
  isOfficeInitialized = true;
  render(App);
};
