import {useCallback, useState} from 'react';
import {loginToAzure, logoutFromAzure} from '../helpers/msalHelpers';

export const useAuthProcess = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);

  const login = useCallback(async () => {
    setIsLoading(true);

    loginToAzure((status) => {
      if (status) {
        setIsLoading(false);
      }
    }, (error) => {
      setError(error);
    });
  }, []);

  const logout = useCallback(async () => {
    setIsLoading(true);

    logoutFromAzure(
      () => {
        setIsLoading(false);
      },
      (error) => {
        setError(error);
      });
  }, []);

  return {isLoading, error, login, logout};
}