import {AuthenticationResult} from '@azure/msal-browser';
import {setIdentityToken} from '../taskpane/state/session/session.service';
import {msalPublicClientApplication} from '../taskpane';

export interface LoginResponse {
  status: string;
  response: AuthenticationResult;
  newLocalStorage: any;
}

let loginDialog: Office.Dialog;
let logoutDialog: Office.Dialog;

const url = location.protocol + '//' + location.hostname + (location.port ? ':' + location.port : '');

export const loginToAzure = (loginComplete, onError) => {
  const fullUrl = url + '/login.html';

  Office.context.ui.displayDialogAsync(fullUrl, {height: 60, width: 30}, function (result) {
    loginDialog = result.value;
    loginDialog.addEventHandler(Office.EventType.DialogMessageReceived, onLoginMessage);
    loginDialog.addEventHandler(Office.EventType.DialogEventReceived, onLoginEvent);
  });

  const onLoginEvent = (arg) => {
    processDialogEvent(arg, onError);
  };

  const onLoginMessage = async ({message}: { message: string, origin: string }) => {
    const {
      status,
      response,
      newLocalStorage
    }: LoginResponse = JSON.parse(message);
    loginDialog.close();

    if (status === 'success') {
      // We now have a valid access token.
      setMsalInformationToLocalStorage(newLocalStorage);
      msalPublicClientApplication.setActiveAccount(response.account);

      location.reload();
    }

    loginComplete(status);
  }
};

export const logoutFromAzure = (logoutComplete, onError: (error: { code: number, name: string, description: string }) => void) => {
  const fullUrl = url + '/logout.html';

  Office.context.ui.displayDialogAsync(fullUrl, {height: 40, width: 30}, result => {
    if (result.status === Office.AsyncResultStatus.Failed) {
      const {code, name, message} = result.error;
      onError({code, name, description: message});
    } else {
      logoutDialog = result.value;
      logoutDialog.addEventHandler(Office.EventType.DialogMessageReceived, onLogoutMessage);
      logoutDialog.addEventHandler(Office.EventType.DialogEventReceived, onLogoutEvent);
    }
  });

  const onLogoutEvent = (arg) => {
    processDialogEvent(arg, onError);
  };

  const onLogoutMessage = () => {
    setIdentityToken(null);
    clearMsalInformationFromLocalStorage();

    logoutDialog.close();
    location.reload();

    logoutComplete();
  };
};

export const clearMsalInformationFromLocalStorage = () => {
  const excludedKeys = [];

  for (const [key] of Object.entries(localStorage)) {
    if (excludedKeys.includes(key)) {
      continue;
    }
    localStorage.removeItem(key);
  }
}

const setMsalInformationToLocalStorage = (localStorageNew) => {
  // Update local storage of taskpane instance
  for (const [key, value] of Object.entries(localStorageNew)) {
    if (typeof value === 'string') {
      localStorage.setItem(key, value);
    }
  }
}

const processDialogEvent = (arg: { error: number, type: string },
                            displayError: (error: { code: number, name: string, description: string }) => void) => {
  switch (arg.error) {
    case 12002:
      displayError({
        code: arg.error,
        name: '',
        description: 'The dialog box has been directed to a page that it cannot find or load, or the URL syntax is invalid.'
      });
      break;
    case 12003:
      displayError({
        code: arg.error,
        name: '',
        description: 'The dialog box has been directed to a URL with the HTTP protocol. HTTPS is required.'
      });
      break;
    case 12006:
      // 12006 means that the user closed the dialog instead of waiting for it to close.
      // It is not known if the user completed the login or logout, so assume the user is
      // logged out and revert to the app's starting state. It does no harm for a user to
      // press the login button again even if the user is logged in.
      break;
    default:
      displayError({code: arg.error, name: '', description: 'Unknown error in dialog box.'});
      break;
  }
};
