import * as React from 'react';
import {useEffect, useState} from 'react';
import {MessageBar, Spinner} from '@fluentui/react';
import {useAuthProcess} from '../../hooks/useAuthProcess';
import {Navbar} from './Navbar/Navbar';
import {Home} from './Home/Home';
import {Toaster} from 'react-hot-toast';
import {setIdentityTokenByCurrentLoggedInUser, setIdentityTokenByOfficeRuntime} from '../state/session/session.service';

export interface AppProps {
  title: string;
  isOfficeInitialized: boolean;
}

export const App = ({}: AppProps) => {
    const {isLoading, error} = useAuthProcess();
    const [needLogin, setNeedLogin] = useState(true);


    useEffect(() => {
      const tryAutomaticLoginForUser = async () => {
        let isTokenAcquired = await setIdentityTokenByOfficeRuntime();

        // If token is not acquired by office runtime fallback to msal
        if (!isTokenAcquired) {
          isTokenAcquired = await setIdentityTokenByCurrentLoggedInUser();
        }

        setNeedLogin(!isTokenAcquired);
      }

      tryAutomaticLoginForUser();
    }, []);

    return (
      <>
        <Navbar needLogin={needLogin}/>

        {isLoading || needLogin
          ? <Spinner className="spinnerTop"/>
          : <div className="container">
            {error && <MessageBar>{error.description}</MessageBar>}

            {!needLogin &&
              <Home/>
            }
          </div>
        }

        <Toaster/>
      </>
    );
  }
;
